  height: 100vh;
  flex-direction: column;
  .chat {
    width: 100%;
    min-height: 20vh;
    overflow-y: scroll;
    // border: 2px solid rgb(199, 199, 199);
  }
  .formSend {
    display: flex;
    flex-direction: row;
    justify-content: center;
